import React, { useContext, useState } from 'react';
import { AppContext } from '../../../context/context';
import Checkbox from '../../shared/Checkbox';
import Dropdown from '../../shared/Dropdown';
import Input from '../../shared/Input';
import ModuleOptions from '../ModuleOptions';

interface ModuleOptionsProps {
  reset: boolean
}

export default function ModuleTwoOptions({ reset }: ModuleOptionsProps) {
  const [, dispatch] = useContext(AppContext);

  return (
    <ModuleOptions title={"Brain Segmentation Options (Module 2)"}>
      <div className="flex fill col-12 ">
        <div className="col-12 flex column py-2 px-0">
          <b className="fit p-0 flex">
            General Options
          </b>
          <Input
            label="Data Output Path"
            defaultValue={"C:\\Workspaces\\carl"}
            reset={reset}
            placeholder="Your path goes here"
            name="dataOutput"
            module="moduleTwo" />
          <Checkbox
            label="Restart Enabled"
            defaultValue={true}
            reset={reset}
            name="restartEnabled"
            module="moduleTwo" />
          <Input
            label="Minimum 3D"
            defaultValue={"0.125"}
            reset={reset}
            placeholder=""
            name="minimum3d"
            module="moduleTwo" />
        </div>

        <div className="col-12 flex py-2 px-0">
          <b className="col-12 p-0 flex">
            Data Source
        </b>
          <Dropdown
            label="Modality"
            defaultValue={0}
            reset={reset}
            options={["CT", "MR"]}
            name="modality"
            module="moduleTwo" />
          <Dropdown
            label="Vendor"
            defaultValue={0}
            reset={reset}
            options={["Toshiba", "GE"]}
            name="vendor"
            module="moduleTwo" />
          <Dropdown
            label="Scanner"
            defaultValue={0}
            reset={reset}
            options={["320", "LS"]}
            name="scanner"
            module="moduleTwo" />
          <Dropdown
            label="Process"
            defaultValue={0}
            reset={reset}
            options={["DCE", "Single"]}
            name="process"
            module="moduleTwo" />
          <Dropdown
            label="Region"
            defaultValue={0}
            reset={reset}
            options={["Brain"]}
            name="region"
            module="moduleTwo" />
          <Dropdown
            label="Study"
            defaultValue={0}
            reset={reset}
            options={["ICH", "dCTA", "CTP"]}
            name="study"
            module="moduleTwo" />
        </div>

        <div className="col-12 flex column py-2 px-0">
          <b className="fit p-0 flex">
            Image Set Time Series Thresholding
        </b>
          <Checkbox
            label="Include Maximum Threshold"
            defaultValue={true}
            reset={reset}
            name="includeMinThreshold"
            module="moduleTwo" />
          <Checkbox
            label="Include Minimum Threshold"
            defaultValue={true}
            reset={reset}
            name="includeMaxThreshold"
            module="moduleTwo" />
        </div>

        <div className="col-12 flex column py-2 px-0">
          <b className="fit p-0 flex">
            Tissue Mask Generation
        </b>
          <Checkbox
            label="Use morphological operations"
            defaultValue={true}
            reset={reset}
            name="useMorphologicalOperations"
            module="moduleTwo" />
          <Input
            label="STRFI disk"
            defaultValue={"5"}
            reset={reset}
            name="strfiDisk"
            module="moduleTwo" />
          <Input
            label="STRFI 3D"
            defaultValue={"2"}
            reset={reset}
            name="strfi3d"
            module="moduleTwo" />
        </div>

        <div className="col-12 flex column py-2 px-0">
          <b className="fit p-0 flex">
            Brain Selection Algorithm Parameters
        </b>
          <Input
            label="Image index range"
            defaultValue={"All"}
            reset={reset}
            placeholder=""
            name="imageIndexRange"
            module="moduleTwo" />

          <Input
            label="Image index region"
            defaultValue={"Minimum"}
            reset={reset}
            placeholder=""
            name="imageRegion"
            module="moduleTwo" />

          <Input
            label="minimumConnected"
            defaultValue={"0.2"}
            reset={reset}
            placeholder=""
            name="minimumConnected"
            module="moduleTwo" />

          <Input
            label="Coarse radius"
            defaultValue={"20"}
            reset={reset}
            placeholder=""
            name="coarseRadius"
            module="moduleTwo" />

          <Input
            label="STRFI radius"
            defaultValue={"1.5"}
            reset={reset}
            placeholder=""
            name="strfiRadius"
            module="moduleTwo" />
        </div>
      </div>
    </ModuleOptions>
  )
}