import { faChevronCircleDown, faChevronCircleUp } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState } from 'react';


interface ModuleOptionsProps {
  title: string
}

const ModuleOptions: React.FunctionComponent<ModuleOptionsProps> = (props) => {
  const [collapsed, setCollapsed] = useState(true);

  return (
    <div className="module-container col-12 p-1" >
      <h5 className="space-between header pointer col-12 p-0 flex" onClick={() => setCollapsed(!collapsed)}>
        {props.title}
        <i>
          <FontAwesomeIcon icon={collapsed ? faChevronCircleDown : faChevronCircleUp} />
        </i>
      </h5>
      {!collapsed && (
        <div className="flex text">
          {props.children}
        </div>
      )}
    </div>
  )
}

export default ModuleOptions