import { ModuleFourOptions as mo4, ModuleOneOptions as mo1, ModuleThreeOptions as mo3, ModuleTwoOptions as mo2, Options, PatientData, State } from "./types";
import { initialState } from "./state";
import { StoreActionType } from './constants';
import { formatPatientData, formatBiomarkerData } from "../utils/format";

const massagePatientData = (state: State, patientData: PatientData[]) => {
  const row: PatientData = patientData[0];
  const res = state.nurseView.patients.findIndex(o => o.scanId === row.scanId)
  const docRes = state.doctorView.patients.findIndex(o => o.scanId === row.scanId)
  let newNurseData = [...state.nurseView.patients]
  let newDocData = [...state.doctorView.patients]

  if (row.currentModule === 5) {
    if (res >= 0) {
      newNurseData.splice(res, 1);
      newDocData.push(row);
    }
    else if (docRes >= 0) {
      newDocData[docRes] = row
    }
    else {
      newDocData.push(row)
    }
  }
  else {
    if (res >= 0) {
      newNurseData[res] = row;
    }
    else if (docRes >= 0) {
      newDocData.splice(docRes, 1);
      newNurseData.push(row);
    }
    else {
      newNurseData.push(row)
    }
  }
  return [newNurseData, newDocData]
}


export const reducer = (state = initialState, action: any): State => {
  switch (action.type) {
    case StoreActionType.SET_IS_LOADED:
      return { ...state, doctorView: { ...state.doctorView, isLoaded: action.isLoaded } }
    case StoreActionType.SET_LOADER:
        return { ...state, showLoader: action.showLoader }
    case StoreActionType.SET_SPOT_COLOR:
      return { ...state, visState: { ...state.visState, spotColor: [action.r, action.g, action.b] } }
    case StoreActionType.SET_VOLUME_OPACITY:
      return { ...state, visState: { ...state.visState, volumeOpacity: action.volumeOpacity} }
    case StoreActionType.SET_SHOW_VENOUS_STRUCTURE:
      return { ...state, visState: { ...state.visState, showVenousStructure: action.showVenousStructure} }
    case StoreActionType.SET_SCANID:
      return { ...state, doctorView: { ...state.doctorView, loadedScan: action.loadedScan } }
    case StoreActionType.SET_VOLUME_SET:
      return { ...state, visState: { ...state.visState, volumeSet: action.volumeSet } }
    case StoreActionType.SET_TEXTURE3D:
      return { ...state, visState: { ...state.visState, texture3d: action.texture3d } };
    case StoreActionType.SET_VOLUME_INDEX:
      return { ...state, visState: { ...state.visState, volumeIndex: action.volumeIndex } };
    case StoreActionType.SET_MODE_VIEW:
      return { ...state, visState: { ...state.visState, modeView: action.modeView } }
    case StoreActionType.SET_MODE_2D:
      return { ...state, visState: { ...state.visState, mode2d: action.mode2d } };
    case StoreActionType.SET_SLIDER_2D:
      return { ...state, visState: { ...state.visState, slider2d: action.slider2d } };
    case StoreActionType.SET_MODE_3D:
      return { ...state, visState: { ...state.visState, mode3d: action.mode3d } };
    case StoreActionType.SET_MODE_3Droi:
      return { ...state, visState: { ...state.visState, mode3droi: action.mode3droi } };
    case StoreActionType.SET_SLIDER_3DR:
      return { ...state, visState: { ...state.visState, slider3d_r: action.slider3d_r } };
    case StoreActionType.SET_SLIDER_3DB:
      return { ...state, visState: { ...state.visState, slider3d_b: action.slider3d_b } };
    case StoreActionType.SET_SLIDER_3DG:
      return { ...state, visState: { ...state.visState, slider3d_g: action.slider3d_g } };
    case StoreActionType.SET_SLIDER_Opacity:
      return { ...state, visState: { ...state.visState, sliderOpacity: action.sliderOpacity } };
    case StoreActionType.SET_SLIDER_Isosurface:
      return { ...state, visState: { ...state.visState, sliderIsosurface: action.sliderIsosurface } };
    case StoreActionType.SET_SLIDER_ErRadius:
      return { ...state, visState: { ...state.visState, sliderErRadius: action.sliderErRadius } };
    case StoreActionType.SET_SLIDER_ErDepth:
      return { ...state, visState: { ...state.visState, sliderErDepth: action.sliderErDepth } };
    case StoreActionType.SET_VOLUME_Renderer:
      return { ...state, visState: { ...state.visState, volumeRenderer: action.volumeRenderer } };
    case StoreActionType.SET_SLIDER_Brightness:
      return { ...state, visState: { ...state.visState, sliderBrightness: action.sliderBrightness } };
    case StoreActionType.SET_SLIDER_Cut:
      return { ...state, visState: { ...state.visState, sliderCut: action.sliderCut } };
    case StoreActionType.SET_SLIDER_Quality:
      return { ...state, visState: { ...state.visState, sliderQuality: action.sliderQuality } };
    case StoreActionType.SET_2D_TOOLS_INDEX:
      return { ...state, visState: { ...state.visState, indexTools2d: action.indexTools2d } };
    case StoreActionType.SET_2D_X_POS:
      return { ...state, visState: { ...state.visState, render2dxPos: action.render2dxPos } };
    case StoreActionType.SET_2D_ZOOM:
      return { ...state, visState: { ...state.visState, render2dZoom: action.render2dZoom } };
    case StoreActionType.SET_2D_Y_POS:
      return { ...state, visState: { ...state.visState, render2dyPos: action.render2dyPos } };
    case StoreActionType.SET_UI_APP:
      return { ...state, visState: { ...state.visState, uiApp: action.uiApp } };
    case StoreActionType.SET_DICOM_INFO:
      return { ...state, visState: { ...state.visState, dicomInfo: action.dicomInfo } };
    case StoreActionType.SET_IS_TOOL3D:
      return { ...state, visState: { ...state.visState, isTool3D: action.isTool3D } };
    case StoreActionType.SET_GRAPHICS_2D:
      return { ...state, visState: { ...state.visState, graphics2d: action.graphics2d } };
    case StoreActionType.SET_SLIDER_Contrast3D:
      return { ...state, visState: { ...state.visState, sliderContrast3D: action.sliderContrast3D } };
    case StoreActionType.SET_ERR_ARRAY:
      return { ...state, visState: { ...state.visState, arrErrors: action.arrErrors } };
    case StoreActionType.SET_DICOM_SERIES:
      return { ...state, visState: { ...state.visState, dicomSeries: action.dicomSeries } };
    case StoreActionType.SET_LOADER_DICOM:
      return { ...state, visState: { ...state.visState, loaderDicom: action.loaderDicom } };
      case StoreActionType.SET_3D_ZOOM:
      return { ...state, doctorView: { ...state.doctorView, zoom: action.zoom } };
    case StoreActionType.SET_PATIENT_DATA: {
      const patientData: PatientData[] = formatPatientData(action.payload)
      return {
        ...state,
        doctorView: { ...state.doctorView, patients: patientData.filter(patient => patient.currentModule === 5) },
        nurseView: { ...state.nurseView, patients: patientData.filter(patient => patient.currentModule !== 5) }
      };
    };
    case StoreActionType.UPDATE_PATIENT_DATA: {
      const patientData = formatPatientData([action.payload])
      const [newNurseData, newDocData] = massagePatientData(state, patientData);
      return {
        ...state,
        doctorView: { ...state.doctorView, patients: newDocData },
        nurseView: { ...state.nurseView, patients: newNurseData }
      };
    };
    case StoreActionType.UPDATE_BIOMARKER_DATA: {
      const biomarkerData = formatBiomarkerData([action.payload])[0]
      console.log(biomarkerData); // TODO actually do something with this
      if (biomarkerData.marker === "V0") {
        state.doctorView.volumeOfLeakage = biomarkerData.value;
      }
      else if (biomarkerData.marker === "K1") {
        state.doctorView.rateOfLeakage = biomarkerData.value;
      }

      if (state.doctorView.rateOfLeakage !== -1 && state.doctorView.volumeOfLeakage !== -1) {
        state.doctorView.timeOfStroke = Math.round(state.doctorView.volumeOfLeakage / state.doctorView.rateOfLeakage * 100) / 100;
        state.doctorView.status = "Bleeding";
      }
      return {
        ...state,
      };
    };
    case StoreActionType.SET_NOTIFICATION: {
      return {
        ...state,
        notification: action.payload,
      };
    }
    case StoreActionType.SET_PHASETIMES:
      return {
        ...state,
        doctorView: {
          ...state.doctorView,
          phaseTimes: action.payload
        }
      }
    case StoreActionType.SET_INPUT:
      const {
        module, name, value
      } = action.payload
      const newState = {
        ...state,
        options: {
          ...state.options
        }
      };
      newState.options.moduleOne = { ...state.options.moduleOne }
      newState.options.moduleTwo = { ...state.options.moduleTwo }
      newState.options.moduleThree = { ...state.options.moduleThree }
      newState.options.moduleFour = { ...state.options.moduleFour }
      // heck you typescript for being annoying
      // @ts-ignore
      newState.options[module as keyof Options][name] = value as any;
      return newState;
    default: return { ...state }
  }
}