import React, { useEffect, useContext, useState } from 'react';
import { Form } from 'react-bootstrap';
import { StoreActionType } from '../../context/constants';
import { AppContext } from '../../context/context';

interface CheckboxProps {
  label: string,
  defaultValue: boolean,
  reset: boolean,
  module: string,
  name: string
}

export default function Checkbox({ label, reset, defaultValue, module, name }: CheckboxProps) {

  const [state, dispatch] = useContext(AppContext);
  // Hecking typescript
  // @ts-ignore
  const optionModule = state.options[module as keyof Options]
  const value = optionModule[name]

  useEffect(() => {
    console.log(reset);
    if (reset) {
      dispatchValue(defaultValue)
    }
  }, [reset])

  const dispatchValue = (value: boolean) => {
    dispatch({
      type: StoreActionType.SET_INPUT,
      payload: {
        module, name, value
      }
    })
  }

  const handleChange = (e: React.ChangeEvent<any>) => {
    dispatchValue(!value)
  }

  return (
    <Form.Group controlId="formBasicPassword" className="flex left">
      <Form.Check
        type={'checkbox'}
        checked={value}
        label={label}
        onChange={e => handleChange(e)}
      />
    </Form.Group>)
}