import React, { useContext, useEffect, useState } from 'react';
import { Nav, Navbar as BootstrapNavbar } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import { AppContext } from '../../context/context';
import UiOpenMenu from '../visualization/ui/UiOpenMenu';
import { useLocation } from "react-router-dom";

export default function Navbar() {
  const [state, dispatch] = useContext(AppContext);
  let location = useLocation();
  useEffect(() => {
    let fileNameOnLoad = '';
    const strSearch = window.location.search;
    if (strSearch.length > 0) {
      // console.log(`UiApp. componentDidMount. app search = ${strSearch}`);
      const strReg = /\\?url=(\S+)/;
      const arr = strSearch.match(strReg);
      if (arr === null) {
        console.log('arguments should be in form: ?url=www.xxx.yy/zz/ww');
        return;
      }
      fileNameOnLoad = arr[1];

      const regA = /^((ftp|http|https):\/\/)?(([\S]+)\.)?([\S]+)\.([A-z]{2,})(:\d{1,6})?\/[\S]+/;
      const regB = /(ftp|http|https):\/\/([\d]+)\.([\d]+)\.([\d]+)\.([\d]+)(:([\d]+))?\/([\S]+)/;
      const isValidA = fileNameOnLoad.match(regA);
      const isValidB = fileNameOnLoad.match(regB);
      if ((isValidA === null) && (isValidB === null)) {
        console.log(`Not valid URL = ${fileNameOnLoad}`);
        return;
      }
      setm_fileNameOnLoad(fileNameOnLoad);
    }
  }, [])

  const [m_fileNameOnLoad, setm_fileNameOnLoad] = useState("")
  return (
    <BootstrapNavbar expand="lg" className="fit">
      <BootstrapNavbar.Toggle aria-controls="basic-navbar-nav" />
      <BootstrapNavbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <UiOpenMenu store={state} dispatch={dispatch} fileNameOnLoad={m_fileNameOnLoad} />
        </Nav>
        <NavLink className="p-1" activeStyle={{fontWeight: 'bold' }} exact to="/frontend-demo">Home</NavLink>
        <NavLink className="p-1" activeStyle={{fontWeight: 'bold' }} exact to="/frontend-demo/doctor">Doctor</NavLink>
        <NavLink className="p-1" activeStyle={{fontWeight: 'bold' }} exact to="/frontend-demo/nurse">Nurse</NavLink>
        <NavLink className="p-1" activeStyle={{fontWeight: 'bold' }} exact to="/frontend-demo/radiologist">Options</NavLink>
      </BootstrapNavbar.Collapse>
    </BootstrapNavbar>)
}
