import React, { useContext, useEffect, useState } from 'react';
import { Col, ProgressBar, Dropdown } from 'react-bootstrap';
import Nouislider from 'react-nouislider';
import Graphics3d from '../../components/visualization/engine/Graphics3d';
import { Modes3d, StoreActionType } from '../../context/constants';
import { AppContext } from '../../context/context';
import PatientList from '../shared/PatientList';
import wNumb from 'wnumb';
import Loader from "react-loader-spinner";
//styles
import "./DoctorView.scss";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";


function DoctorView() {
  const [state, dispatch] = useContext(AppContext)
  const [showProgressBar, setShowProgressBar] = useState(false)
  const [strProgressMessage, setMessage] = useState("Loading...");
  const [progressBarRatio, setRatio] = useState(0);
  const [spinning, toggleSpin] = useState(false);
  const options = ['Anterior Axial', 'Coronal'];
  const [currentOption, setOption] = useState(0);

  const graphics3d = React.useRef<Graphics3d>(null);
  const slider = React.useRef<Nouislider>(null);
  const opacitySlider = React.useRef<Nouislider>(null);
  const zoomSlider = React.useRef<Nouislider>(null);

  const volSet = state.visState.volumeSet;
  const volumeIndex = [state.visState.volumeIndex];

  const sliderOpacity = [state.visState.volumeOpacity];
  const isNormalView = state.visState.mode3d === Modes3d.RAYCAST;
  const showVenousStructure = state.visState.showVenousStructure;

  const MIN_ZOOM = 0.5;
  const MAX_ZOOM = 2;

  let numVolumes = 0;
  if (volSet !== null && volSet !== undefined) {
    numVolumes = volSet.getNumVolumes();
  }

  const closeVis = () => {
    dispatch({ type: StoreActionType.SET_LOADER, showLoader: false});
    // dispatch({ type: StoreActionType.SET_VOLUME_SET, volumeSet: null });
    // dispatch({ type: StoreActionType.SET_VOLUME_INDEX, volumeIndex: 0 });
    dispatch({ type: StoreActionType.SET_IS_LOADED, isLoaded: false });
    // dispatch({ type: StoreActionType.SET_FILENAME, fileName: "" });
    // dispatch({ type: StoreActionType.SET_ERR_ARRAY, arrErrors: [] });
    // dispatch({ type: StoreActionType.SET_TEXTURE3D, texture3d: null });
  }

  const timeConversion = (time: number) => {

    const milliseconds = (time * 1000)

    const microseconds = (milliseconds * 1000)

    const nanoseconds = (microseconds * 1000000000)

    if (milliseconds > 1) {
      return milliseconds.toFixed(0);
    } else if (microseconds > 1) {
      return microseconds.toFixed(0);
    } else if (nanoseconds > 1) {
      return nanoseconds.toFixed(0);
    } else {
      return time.toFixed(2);
    }
  }

  useEffect(() => {
    class appUI {
      doHideProgressBar() {
        setShowProgressBar(false)
      }
      doShowProgressBar(str: string) {
        setMessage(str)
        setShowProgressBar(true)
      }
      doSetProgressBarRatio(ratio: number) {
        if ((ratio >= 0) && (ratio <= 99)) {
          if (!showProgressBar) {
            setShowProgressBar(true)
          }
          setRatio(ratio)
        }
      }
    }
    const app = new appUI()
    dispatch({
      type: StoreActionType.SET_UI_APP,
      uiApp: app
    });

    dispatch({
      type: StoreActionType.SET_VOLUME_INDEX,
      volumeIndex: numVolumes - 1
    })
  }, [])

  const objPrgBarVis = (
    <div className="col-12 flex center" style={{ marginTop: 'auto' }}>
      <Col xs xl sm md lg="12" style={{ width: '100%', color: 'whitesmoke' }}>
        {strProgressMessage}
        <ProgressBar animated variant="success"
          now={progressBarRatio} label={`${progressBarRatio}%`} />
      </Col>
    </div>)

  const onChangePhase = (target: string[]) => {
    if (graphics3d.current && graphics3d.current.isLoaded) // we dont want to load new phases until the last one is finished
      dispatch({ type: StoreActionType.SET_VOLUME_INDEX, volumeIndex: Number.parseInt(target[0]) });
    else if (slider.current)
      slider.current.set(state.visState.volumeIndex)

  }

  const onChangeZoom = (zoom: number, increment?: number) => {
    zoom = zoom / 10;
    zoom = Number.parseFloat(zoom.toFixed(1));
    if (increment !== undefined) {
      if (increment === 1) {
        zoom += 0.1;
        zoom = zoom > MAX_ZOOM ? MAX_ZOOM : zoom;
      }
      else {
        zoom -= 0.1;
        zoom = zoom < MIN_ZOOM ? MIN_ZOOM : zoom;
      }
    }
    dispatch({ type: StoreActionType.SET_3D_ZOOM, zoom: zoom });
    state.visState.volumeRenderer.orbitControl.setZoom(zoom);
  }

  const dateFormatting = (date: Date) => {
    const time = date.toDateString()
    return time;
  };

  const toggleSpot = () => {
    if (isNormalView) {
      dispatch({ type: StoreActionType.SET_MODE_3D, mode3d: Modes3d.RAYFAST });
    }
    else {
      dispatch({ type: StoreActionType.SET_MODE_3D, mode3d: Modes3d.RAYCAST });
    }
  }

  const toggleVenousStructure = () => {
    dispatch({ type: StoreActionType.SET_SHOW_VENOUS_STRUCTURE, showVenousStructure: !showVenousStructure });
  }

  const timeUnit = (time: number) => {

    const milliseconds = (time * 1000)

    const microseconds = (milliseconds * 1000)

    const nanoseconds = (microseconds * 1000000000)

    if (milliseconds > 1) {
      return 'ms'
    } else if (microseconds > 1) {
      return 'us'
    } else if (nanoseconds > 1) {
      return 'ns'
    } else {
      return 's'
    }
  }

  const handleSliderChange = (opacity: number) => {
    dispatch({ type: StoreActionType.SET_VOLUME_OPACITY, volumeOpacity: opacity / 100 });
  }

  const setView = (key: any, e: any) => {
      const newViewMode = Number.parseInt(e.target.id);
      if (state.visState.volumeRenderer) {
          state.visState.volumeRenderer.setPresetView(newViewMode);
          setOption(newViewMode);
      }
  }

  return (
    <div className="col-12 p-0 flex fill">
      {!state.doctorView.isLoaded && <PatientList isNurse={false} />}
      {state.doctorView.isLoaded && (<div className="overlay flex">
        <div className="col-3 p-3 flex left">
          <div className="col-12 p-0 flex left data-header">
            Status
          </div>
          <h3 className="data-large">
            {state.doctorView.status}
          </h3>
          <div className="col-12 p-0 flex left data-header">
            Est. Rate of Leakage
          </div>
          <h3 className="data-large">
            {state.doctorView.rateOfLeakage} ml/min
          </h3>
          <div className="col-12 p-0 flex left data-header">
            Est. Time of Stroke
          </div>
          <h3 className="data-large">
            {state.doctorView.timeOfStroke / 60} min
          </h3>
          {/* note: should only allow button to be selected if a spot is identified */}
          <div className="col-12 p-0 flex left">
            <button className="button-main" style={{ 'background': isNormalView ? '' : 'dimgrey' }} onClick={() => toggleSpot()}>Reveal SPOT</button>
          </div>
          {
            state.visState.volumeRenderer &&
          <div className="col-12 p-0 flex left">
            <button className="button-main" style={{ 'background': !spinning ? '' : 'dimgrey' }} onClick={() => toggleSpin(state.visState.volumeRenderer.setAutoSpin(!spinning))}>Auto rotate</button>
          </div>
          }
          <div className="col-12 p-0 flex left">
            <button className="button-main" style={{ 'background': !showVenousStructure ? '' : 'dimgrey' }} onClick={() => toggleVenousStructure()}>Show Venous Structure</button>
          </div>
          <div className="col-12 p-0 flex left">
            <div className="data-header">
                Orientation
            </div>
            <Dropdown className="dropdown" id="dropdown-basic-button" onSelect={setView}>
                <Dropdown.Toggle id={JSON.stringify(Math.random())}>{options[currentOption]}</Dropdown.Toggle>
                <Dropdown.Menu >
                    {options.map((option, index) => (
                        <Dropdown.Item id={index}>{option}</Dropdown.Item>)
                    )}
                </Dropdown.Menu>
            </Dropdown>
        </div>
          <div className="col-auto p-0 flex column small-slider vertical">
            <i className="fas fa-search-plus mb-3" style={{ color: 'white', cursor: 'pointer' }} onClick={() => onChangeZoom(zoomSlider.current.slider.get(), 0)}></i>
            <Nouislider onSlide={(data: any) => onChangeZoom(data)}
              range={{ min: MIN_ZOOM * 10, max: MAX_ZOOM * 10 }}
              animate={false}
              ref={zoomSlider}
              orientation="vertical"
              // style={{ height: 100 }}
              width={10}
              start={[state.doctorView.zoom * 10]} connect={[false, true]} step={1} tooltips={false}
              format={wNumb({ decimals: 1 })} />
            <i className="fas fa-search-minus  mt-3" style={{ color: 'white', cursor: 'pointer' }} onClick={() => onChangeZoom(zoomSlider.current.slider.get(), 1)}></i>
          </div>
        </div>
        <div className="col-6 p-0">
        </div>
        <div className="col-3 p-4 flex right top">
          <div className="col-12 p-0 data-header flex right">
            John Doe
          </div>
          <div className="col-12 p-0 data-header flex right">
            {state.doctorView.title}
          </div>
          <div className="col-12 p-0 data-header flex right">
            {dateFormatting(state.doctorView.date)}
          </div>
          <div className="col-12 p-0 data-header flex right">
            Age: 72
          </div>
          <div className="col-12 p-0 data-header flex right">
            Sex: M
          </div>
          <div className="col-12 p-0">
            <button className="button-main" onClick={() => closeVis()}>Close</button>
          </div>
          <div className="col-12 pt-2 px-0 flex data-header right">
            <b>
              Volume Opacity: {(state.visState.volumeOpacity * 100).toFixed(0)}%
            </b>
          </div>
          <div className="col-auto p-0 flex small-slider vertical">
            <Nouislider onSlide={(data: any) => handleSliderChange(data)}
              range={{ min: 0, max: 100 }}
              ref={opacitySlider}
              direction="rtl"
              start={[state.visState.volumeOpacity * 100]}
              connect={[true, false]}
              orientation="vertical"
              step={1} />
          </div>
        </div>
        <div className="col-12 p-0 flex center slider-container">
          <div className="col-8 p-4 slider">
            {
              numVolumes > 1 &&
              <Nouislider onSet={(data: any) => onChangePhase(data)}
                range={{ min: 0, max: numVolumes - 1 }}
                // range={{ min: 0, max: 10 }}
                ref={slider}
                style={{ height: 100, cursor: 'pointer' }}
                start={volumeIndex} connect={[true, false]} step={1} tooltips={true} format={wNumb({ decimals: 0 })} />
            }
            {state.doctorView.phaseTimes.length > 0 && numVolumes > 1 &&
              <div className="col-12 p-0 flex space-between">
                <div className="data-header flex center column tick">
                  <div className="tick-line" />
                  <h5>0s</h5>
                </div>
                {state.doctorView.phaseTimes.map(num => (
                  <div className="data-header flex center column tick">
                    <div className="tick-line" />
                    <h5>{num}s</h5>
                  </div>
                ))}
              </div>}
          </div>
        </div>
      </div>
      )}

        <div className="loader-container">
            <Loader
                type="ThreeDots"
                color="#95afcd"
                height={250}
                width={250}
                visible={state.showLoader}
            />
        </div>
      {state.doctorView.isLoaded &&
        (<div className="flex col-12">
          <div className="col-12 flex graphics-center">
            <Graphics3d ref={graphics3d} store={state} dispatch={dispatch} />
          </div>
        </div>
        )}

      {showProgressBar ? objPrgBarVis : null}
    </div >
  );
}

export default DoctorView;
