import React, { useContext, useState } from 'react';
import { AppContext } from '../../../context/context';
import Checkbox from '../../shared/Checkbox';
import Dropdown from '../../shared/Dropdown';
import Input from '../../shared/Input';
import ModuleOptions from '../ModuleOptions';

interface ModuleOptionsProps {
  reset: boolean
}

export default function ModuleOneOptions({ reset }: ModuleOptionsProps) {
  const [, dispatch] = useContext(AppContext);

  return (
    <ModuleOptions title={"Preprocessing Options (Module 1)"}>
      <div className="flex fill">
        <div className="col-6">
          <Checkbox
            label="Process Timing Data"
            defaultValue={true}
            reset={reset}
            name="processTimingData"
            module="moduleOne" />
          <Checkbox
            label="Build 4D Image Data Set"
            defaultValue={true}
            reset={reset}
            name="build4d"
            module="moduleOne" />
          <Checkbox
            label="Build 3D Image Data Set"
            defaultValue={true}
            reset={reset}
            name="build3d"
            module="moduleOne" />

          <Checkbox
            label="Compute 4D Summary"
            defaultValue={true}
            reset={reset}
            name="compute4dSummary"
            module="moduleOne" />

          <Checkbox
            label="Save available data"
            defaultValue={true}
            reset={reset}
            name="saveAvailableData"
            module="moduleOne" />
        </div>

        <div className="col-6">

          <Checkbox
            label="Reload timing indices"
            defaultValue={true}
            reset={reset}
            name="reloadTimingIndices"
            module="moduleOne" />

          <Checkbox
            label="Stop if options change"
            defaultValue={false}
            reset={reset}
            name="stopIfOptionsChange"
            module="moduleOne" />

          <Checkbox
            label="Auto select largest"
            defaultValue={true}
            reset={reset}
            name="autoSelectLargest"
            module="moduleOne" />
          <Checkbox
            label="Auto select first largest"
            defaultValue={false}
            reset={reset}
            name="autoSelectFirstLargest"
            module="moduleOne" />
          <Checkbox
            label="Auto select last largest"
            defaultValue={true}
            reset={reset}
            name="autoSelectLastLargest"
            module="moduleOne" />
        </div>

        <div className="col-12 flex left">
          <b className="col-12 p-0 flex left">
            Set time Separation
          </b>
          <div className="col-12 p-0 flex">

            <Input
              label="Min"
              defaultValue={"0"}
              reset={reset}
              placeholder=""
              name="setTimeSeparationMin"
              module="moduleOne" />

            <Input
              label="Max"
              defaultValue={"60"}
              reset={reset}
              placeholder=""
              name="setTimeSeparationMax"
              module="moduleOne" />


          </div>
          <Input
            label="Specify image set index"
            defaultValue={"0"}
            reset={reset}
            placeholder=""
            name="imageSetIndex"
            module="moduleOne" />
        </div>
      </div>
    </ModuleOptions>
  )
}