import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useContext } from 'react';
import { StoreActionType } from '../../context/constants';
import { AppContext } from '../../context/context';
import { Notification as NotificationType } from '../../context/types';

interface LoadingCircleProps {
  notification: NotificationType
}

export default function Notification({ notification }: LoadingCircleProps) {
  const [state, dispatch] = useContext(AppContext)
  const { header, body, isVisible, timer, icon, spin, status } = notification;

  if (timer) {
    setTimeout(() => {
      dispatch({
        type: StoreActionType.SET_NOTIFICATION,
        payload: {
          body: '',
          header: '',
          isVisible: false,
        }
      });
    }, timer)
  }

  if (isVisible || timer) {
    return (
      <div className={`notification flex center column ${status}`}>
        <div className="notification-header">
          {icon && <FontAwesomeIcon icon={icon} spin={spin} className="mr-2" />}
          {header}
        </div>
        <div className="notification-body">
          {body}
        </div>
      </div>
    );
  }
  return null;
}