import React, { useContext, useState } from 'react';
import { AppContext } from '../../../context/context';
import Checkbox from '../../shared/Checkbox';
import Dropdown from '../../shared/Dropdown';
import Input from '../../shared/Input';
import ModuleOptions from '../ModuleOptions';

interface ModuleOptionsProps {
  reset: boolean
}

export default function ModuleThreeOptions({ reset }: ModuleOptionsProps) {
  const [, dispatch] = useContext(AppContext);

  return (
    <ModuleOptions title={"Adaptive Vascular Model Options (Module 3)"}>
      <div className="flex fill col-12">
        <div className="col-12 flex column py-2 px-0">
          <b className="fit p-0 flex">
            Binning Options
            </b>

          <Input
            label="Minimum number of bins"
            defaultValue={"50"}
            reset={reset}
            placeholder="Your path goes here"
            name="minimumNumber"
            module="moduleThree" />

          <Input
            label="Number of bins"
            defaultValue={"400"}
            reset={reset}
            placeholder="Your path goes here"
            name="numberOfBins"
            module="moduleThree" />

          <Input
            label="Distribution Tail"
            defaultValue={"0.01"}
            reset={reset}
            placeholder="Your path goes here"
            name="minimumNumber"
            module="moduleThree" />

          <Dropdown
            label="Feature"
            defaultValue={0}
            reset={reset}
            options={["Median Value", "Mode Value", "Cumulative Probability"]}
            name="feature"
            module="moduleThree" />
        </div>
        <div className="col-12 flex column py-2 px-0">
          <b className="fit p-0 flex">
            Distribution Feature Range
          </b>
          <Input
            label="Median Min"
            defaultValue={"0.3"}
            reset={reset}
            name="medianMin"
            module="moduleThree" />

          <Input
            label="Median Max"
            defaultValue={"0.4"}
            reset={reset}
            name="medianMax"
            module="moduleThree" />
          <Checkbox
            label="Non-negative values"
            defaultValue={true}
            reset={reset}
            name="nonNegativeValues"
            module="moduleThree" />
          <Checkbox
            label="Non-zero values"
            defaultValue={true}
            reset={reset}
            name="nonZeroValues"
            module="moduleThree" />
        </div>
      </div>
    </ModuleOptions>
  )
}