import React, { useContext, useState } from 'react';
import { AppContext } from '../../../context/context';
import Checkbox from '../../shared/Checkbox';
import Input from '../../shared/Input';
import ModuleOptions from '../ModuleOptions';

interface ModuleOptionsProps {
  reset: boolean
}

export default function ModuleFourOptions({ reset }: ModuleOptionsProps) {
  const [, dispatch] = useContext(AppContext);

  return (
    <ModuleOptions title={"Dynamic Spot Extraction Options (Module 4)"}>
      <div className="flex column fill col-12">
        <b className="fit p-0 flex">
          General Options
          </b>
        <Checkbox
          label="Restart Enabled"
          defaultValue={true}
          reset={reset}
          name="enableRestart"
          module="moduleFour" />
        <Input
          label="Vascular Mask Data Directory"
          defaultValue={"C:\\Workspaces\\carl"}
          reset={reset}
          placeholder="Your path goes here"
          name="vascularMaskDirectory"
          module="moduleTwo" />
        <Input
          label="Output Data Directory"
          defaultValue={"C:\\Workspaces\\carl"}
          reset={reset}
          placeholder="Your path goes here"
          name="outputDataDirectory"
          module="moduleTwo" />
      </div>
    </ModuleOptions >
  )
}