import React, { useState, useEffect} from 'react';
import { Button } from 'react-bootstrap';
import ModuleFourOptions from './module-options/ModuleFourOptions';
import ModuleOneOptions from './module-options/ModuleOneOptions';
import ModuleThreeOptions from './module-options/ModuleThreeOptions';
import ModuleTwoOptions from './module-options/ModuleTwoOptions';
import './RadiologistView.scss';

export default function RadiologistView() {
  const [reset, setReset] = useState(false);

  useEffect(() => {
    if (reset) {
      setReset(false);
    }
  }, [reset])

  return (
    <div className="flex center page">
      <div className="col-2" />
      <div className="col-8 py-3 px-0 flex options-container">
        <h4 className="header col-12 flex space-between">
          Options
        <Button variant="light" onClick={() => { setReset(true); }}>
            Reset to Default
        </Button>
        </h4>
        <ModuleOneOptions reset={reset}></ModuleOneOptions>
        <ModuleTwoOptions reset={reset}></ModuleTwoOptions>
        <ModuleThreeOptions reset={reset}></ModuleThreeOptions>
        <ModuleFourOptions reset={reset}></ModuleFourOptions>
      </div>
      <div className="col-2" />

    </div >)
}