/* eslint-disable no-fallthrough */
import { faSpinner, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { Dispatch } from "react";
import Texture3D from "../components/visualization/engine/Texture3D";
import Volume from "../components/visualization/engine/Volume";
import VolumeSet from "../components/visualization/engine/VolumeSet";
import { StoreActionType, ModeView, Modes3d } from "../context/constants";
import { NotificationStatus } from "../context/types";
import HttpClient from './httpClient';
// eslint-disable-next-line import/no-webpack-loader-syntax
import FileWorker from "worker-loader!./file-worker"

export const runFileWorker = async (dispatch: Dispatch<any>, scanId: string) => {
  const api = new HttpClient();

  dispatch({
    type: StoreActionType.SET_NOTIFICATION,
    payload: {
      body: '',
      header: 'Loading 4D Image',
      isVisible: true,
      icon: faSpinner,
      spin: true,
      timer: null
    }
  });


  const res = await api.getFileName(scanId);

  if (res instanceof Error) {
    dispatch({
      type: StoreActionType.SET_NOTIFICATION,
      payload: {
        body: 'Couldn\'t find an image',
        header: 'Loading failed',
        isVisible: false,
        status: NotificationStatus.FAILURE,
        icon: faTimesCircle,
        timer: 4000
      }
    });
    return
  }
  console.log(res);

  const worker = new FileWorker()
  worker.postMessage({ data: res.data, scanId });
  worker.onerror = (err) => {
    worker.terminate()
    dispatch({
      type: StoreActionType.SET_NOTIFICATION,
      payload: {
        body: 'An error occurred while loading the image.',
        header: 'Loading failed',
        isVisible: false,
        status: NotificationStatus.FAILURE,
        icon: faTimesCircle,
        timer: 4000
      }
    });

    console.log(err)
  };

  let hydratedTex3d: Texture3D;
  let volumeSetWithData: VolumeSet;
  let m_volumes: Volume[] = [];

    // api.getTiming(scanId).then(function(response) {
    //     //expecting array of timing differences in seconds, elements should be one less than phases
    //     if (response.status === 200 && response.data.length > 0) {
    //         console.log("dispatching phase");
    //         dispatch({ type: StoreActionType.SET_PHASETIMES, payload: response.data });
    //     }
    // })

  worker.onmessage = (e) => {
    // ctx.postMessage({status: 'start', text3d: JSON.stringify(tex3d)})
    // ctx.postMessage({status: 'vol', volume: JSON.stringify(v), data})
    // ctx.postMessage({status: 'end', volumeSet: JSON.stringify(volumeSet)});
    // ctx.postMessage({status: 'fail'});

    // All these objects are JSON stringified version of themselves
    const { status, volume, volumeSet, tex3d, data } = e.data;

    switch(status) {
      case 'start':
        hydratedTex3d = Object.assign(new Texture3D(), JSON.parse(tex3d));
        console.log(`Volume loaded, transferring from worker...`);
        break;
      case 'vol':
        const hydratedVolume = Object.assign(new Texture3D(), JSON.parse(volume));
        hydratedVolume.m_dataArray = data;
        m_volumes.push(hydratedVolume);
        console.log(`   Got volume ${m_volumes.length}`)
        break;
      case 'end':
        volumeSetWithData= Object.assign(new VolumeSet({}), JSON.parse(volumeSet));
        volumeSetWithData.m_volumes = m_volumes;

        const fullyRehydratedVolumeSet = Object.assign(new VolumeSet({}), volumeSetWithData);

        dispatch({ type: StoreActionType.SET_LOADER, showLoader: true});
        dispatch({ type: StoreActionType.SET_VOLUME_SET, volumeSet: fullyRehydratedVolumeSet });
        dispatch({ type: StoreActionType.SET_VOLUME_INDEX, volumeIndex: m_volumes.length - 1 });
        dispatch({ type: StoreActionType.SET_IS_LOADED, isLoaded: true });
        dispatch({ type: StoreActionType.SET_SCANID, loadedScan: scanId});
        dispatch({ type: StoreActionType.SET_ERR_ARRAY, arrErrors: [] });
        dispatch({ type: StoreActionType.SET_TEXTURE3D, texture3d: hydratedTex3d });
        dispatch({ type: StoreActionType.SET_MODE_VIEW, modeView: ModeView.VIEW_2D });
        dispatch({ type: StoreActionType.SET_MODE_3D, mode3d: Modes3d.RAYCAST });
        dispatch({
          type: StoreActionType.SET_NOTIFICATION,
          payload: {
            body: '',
            header: 'Success',
            isVisible: false,
            status: NotificationStatus.SUCCESS,
            icon: faCheckCircle,
            timer: 4
          }
        });
        console.log(`Volume transferring finished.`);
        worker.terminate();
        break;
      case 'fail':
      default:
        worker.terminate();
    }
  };

}
