import React, { useEffect, useContext, useState } from 'react';
import { Form } from 'react-bootstrap';
import { StoreActionType } from '../../context/constants';
import { AppContext } from '../../context/context';

interface InputProps {
  label: string,
  defaultValue: string,
  placeholder?: string,
  reset: boolean,
  module: string,
  name: string
}

export default function Input({ label, reset, defaultValue, placeholder, module, name }: InputProps) {
  const [state, dispatch] = useContext(AppContext);
  // Hecking typescript
  // @ts-ignore
  const optionModule = state.options[module as keyof Options]
  const value = optionModule[name]

  useEffect(() => {
    if (reset) {
      dispatchValue(defaultValue)
    }
  }, [reset])

  const dispatchValue = (value: string) => {
    dispatch({
      type: StoreActionType.SET_INPUT,
      payload: {
        module, name, value
      }
    })
  }

  const handleChange = (e: React.ChangeEvent<any>) => {
    console.log(e);
    dispatchValue(e.target.value);
  }

  return (
    <div className="flex">
      <Form.Group controlId={value} className="flex left">
        <Form.Label>{label}</Form.Label>
        <Form.Control value={value} type="email" placeholder={placeholder} onChange={e => handleChange(e)} />
      </Form.Group>
    </div >)
}