/**
 * @fileOverview LoaderNifti
 * @author Epam
 * @version 1.0.0
 */


// ********************************************************
// Imports
// ********************************************************

import LoadResult from '../LoadResult';
import UiHistogram from '../../ui/UiHistogram';
import FileLoader from './FileLoader';

// ********************************************************
// Const
// ********************************************************

// const NEED_EVEN_TEXTURE_SIZE = false;
const DEBUG = false;
const MAX_BYTE = 255;

// ********************************************************
// Class
// ********************************************************

/**
 * Class LoaderNifti some text later...
 */
class LoaderNifti {
  /**
   * @param {object} props - props from up level object
   */
  constructor() {
    this.m_littleEndian = true;
    this.m_header = {
      m_id: '',
      m_endianness: 0,
      m_glType: 0,
      m_glTypeSize: 0,
      m_glFormat: 0,
      m_glInternalFormat: 0,
      m_glBaseInternalFormat: 0,
      m_pixelWidth: 0,
      m_pixelHeight: 0,
      m_pixelDepth: 0,
      m_numberOfArrayElements: 0,
      m_numberOfFaces: 0,
      m_numberOfMipmapLevels: 0,
      m_bytesOfKeyValueData: 0
    };
    this.m_xDim = 0;
    this.m_yDim = 0;
    this.m_zDim = 0;
    this.phases = 1;
    this.m_boxSize = {
      x: 0.0,
      y: 0.0,
      z: 0.0
    };
  } // constructor
  /**
  * Read 32 bit integer from input buffer
  * @param {object} buf - source buffer
  * @param {number} off - offset in buffer
  * @return 32 bit integer number
  */
  readIntFromBuffer(buf, off) {
    let res = 0;
    if (this.m_littleEndian) {
      res = ((buf[off + 0]) |
        // eslint-disable-next-line
        (buf[off + 1] << 8) |
        // eslint-disable-next-line
        (buf[off + 2] << 16) |
        // eslint-disable-next-line
        (buf[off + 3] << 24));
    } else {
      res = ((buf[off + 3]) |
        // eslint-disable-next-line
        (buf[off + 2] << 8) |
        // eslint-disable-next-line
        (buf[off + 1] << 16) |
        // eslint-disable-next-line
        (buf[off + 0] << 24));
    }
    return res;
  }
  /**
  * Read 16 bit short integer from input buffer
  * @param {object} buf - source buffer
  * @param {number} off - offset in buffer
  * @return 16 bit short integer number
  */
  readShortFromBuffer(buf, off) {
    let res = 0;
    if (this.m_littleEndian) {
      // eslint-disable-next-line
      res = ((buf[off + 0]) | (buf[off + 1] << 8));
    } else {
      // eslint-disable-next-line
      res = ((buf[off + 1]) | (buf[off + 0] << 8));
    }
    return res;
  }
  /**
  * Read 32 bit float from input buffer
  * @param {object} buf - source buffer
  * @param {number} off - offset in buffer
  * @return float number, loaded from buffer
  */
  readFloatFromBuffer(buf, off) {
    const BYTES_IN_FLOAT = 4;
    const arBuf = new ArrayBuffer(BYTES_IN_FLOAT);
    const dataArray = new DataView(arBuf);
    // eslint-disable-next-line
    dataArray.setUint8(0, buf[off + 0]);
    // eslint-disable-next-line
    dataArray.setUint8(1, buf[off + 1]);
    // eslint-disable-next-line
    dataArray.setUint8(2, buf[off + 2]);
    // eslint-disable-next-line
    dataArray.setUint8(3, buf[off + 3]);
    const res = dataArray.getFloat32(0, this.m_littleEndian);
    return res;
  }
  /**
  * Read from local file buffer
  * @param {object} volDst - Destination volume object array to be fiiied
  * @param {object} arrBuf - source byte buffer
  * @param {func} callbackProgress - function invoked during read
  * @param {func} callbackComplete - function invoked after reading
  * @return true, if success
  */

  /*
    mask is a MASK, all 0, but highlights are 1
    when mask is 0, volDst should be mapped to 0.0 - 250
    when mask is 1, volDst should be 255
  */
  readFromBuffer(volDst, arrBuf, callbackProgress, callbackComplete, mask = null, spotThreshold = 0.75) {
    const bufBytes = new Uint8Array(arrBuf);
    const bufLen = bufBytes.length;

    if (mask !== null && mask.length !== bufLen) throw new Error("check your input arrays");
    const MIN_BUF_SIZE = 8;

    const NIFTI_HEADER_SIZE = 348;
    const MAX_PHASES = 19;
    const voxelOffsetLocation = 108;
    const vox_offset = this.readFloatFromBuffer(bufBytes, voxelOffsetLocation);
    console.log(vox_offset);

    const MAX_BUF_SIZE = (512 * 512 * 320 * MAX_PHASES+vox_offset);

    if (bufLen < MIN_BUF_SIZE) {
      if (callbackComplete) {
        callbackComplete(LoadResult.ERROR_TOO_SMALL_DATA_SIZE, null, 0, null);
      }
      return false;
    }
    if (bufLen > MAX_BUF_SIZE) {
      if (callbackComplete) {
        callbackComplete(LoadResult.ERROR_TOO_LARGE_DATA_SIZE, null, 0, null);
      }
      return false;
    }
    // Nifti file header size is 348 bytes
    if (bufLen < NIFTI_HEADER_SIZE) {
      console.error('Nifti header too small');


      if (callbackComplete) {
        callbackComplete(LoadResult.BAD_HEADER, null, 0, null);
      }
      return false;
    }
    if (DEBUG) console.log(`Nifti loader. Start parse ${bufLen} bytes...`);

    const SIZE_DWORD = 4;
    const SIZE_SHORT = 2;

    let bufOff = 0;
    let headSize = this.readIntFromBuffer(bufBytes, bufOff);
    if (headSize > (2 << 24)) {
      this.m_littleEndian = false;
      headSize = this.readIntFromBuffer(bufBytes, bufOff);
    }

    bufOff += SIZE_DWORD;
    if (headSize !== NIFTI_HEADER_SIZE) {
      console.error(`Nifti first int wrong: ${headSize}, but should be ${NIFTI_HEADER_SIZE}`);
      if (callbackComplete) {
        callbackComplete(LoadResult.BAD_HEADER, null, 0, null);
      }
      return false;
    }

    // data type
    // eslint-disable-next-line
    bufOff += 10;
    // db name
    // eslint-disable-next-line
    bufOff += 18;
    // extents
    bufOff += SIZE_DWORD;
    // session error
    bufOff += SIZE_SHORT;
    // regular
    bufOff += 1;
    // dim info
    bufOff += 1;

    // read number of dimensions
    const numDimensions = this.readShortFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_SHORT;
    const MIN_NUM_DIMS = 3;
    if (numDimensions < MIN_NUM_DIMS) {
      console.error(`Nifti header wrong num dimensions: ${numDimensions}, but should be at least 3`);
      if (callbackComplete) {
        callbackComplete(LoadResult.WRONG_HEADER_DIMENSIONS, null, 0, null);
      }
      return false;
    }
    this.m_xDim = this.readShortFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_SHORT;
    this.m_yDim = this.readShortFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_SHORT;
    // says zDim is 3, should be 320 for 4 dimensions
    // if (numDimensions === 4) {
    //     this.phases = this.readShortFromBuffer(bufBytes, bufOff);
    //     bufOff += SIZE_SHORT;
    // }
    this.m_zDim = this.readShortFromBuffer(bufBytes, bufOff);

    bufOff += SIZE_SHORT;
    if (numDimensions === 4) {
      this.phases = this.readShortFromBuffer(bufBytes, bufOff);
      bufOff += SIZE_SHORT;
      const valuesPerTimePoint = this.readShortFromBuffer(bufBytes, bufOff);
      bufOff += 6;
      if (DEBUG) console.log("values per time point: ", valuesPerTimePoint);
    }
    else {
      bufOff += 8;
    }

    // intent_p1
    bufOff += SIZE_DWORD;
    // intent_p2
    bufOff += SIZE_DWORD;
    // intent_p3
    bufOff += SIZE_DWORD;
    // intent_code
    bufOff += SIZE_SHORT;
    const dataType = this.readShortFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_SHORT;
    const bitPix = this.readShortFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_SHORT;
    const sliceStart = this.readShortFromBuffer(bufBytes, bufOff);
    if (DEBUG) {
      console.log("bits / voxel: ", bitPix);
      console.log("slice start: ", sliceStart);
      console.log("datatype: ", dataType);
    }

    const NIFTI_DATA_TYPE_UINT8 = 2;
    const NIFTI_DATA_TYPE_INT16 = 4;
    const NIFTI_DATA_TYPE_FLOAT32 = 16;
    const NIFTI_DATA_TYPE_INT8 = 256;
    const NIFTI_DATA_TYPE_UINT16 = 512;

    let isDataTypeCorrect = 0;
    isDataTypeCorrect |= (dataType === NIFTI_DATA_TYPE_UINT8);
    isDataTypeCorrect |= (dataType === NIFTI_DATA_TYPE_INT16);
    isDataTypeCorrect |= (dataType === NIFTI_DATA_TYPE_FLOAT32);
    isDataTypeCorrect |= (dataType === NIFTI_DATA_TYPE_INT8);
    isDataTypeCorrect |= (dataType === NIFTI_DATA_TYPE_UINT16);


    if (!isDataTypeCorrect) {
      console.error(`Nifti header read. This data type (${dataType}) is not supported`);
      if (callbackComplete) {
        callbackComplete(LoadResult.WRONG_HEADER_DATA_TYPE, null, 0, null);
      }
      return false;
    }
    const BIT_PIXELS_8 = 8;
    const BIT_PIXELS_16 = 16;
    const BIT_PIXELS_32 = 32;
    const isSupported = (bitPix === BIT_PIXELS_8) |
      (bitPix === BIT_PIXELS_16) | (bitPix === BIT_PIXELS_32);
    if (!isSupported) {
      console.error(`Nifti wrong bitPix: ${bitPix}, but should be 8,16 or 32`);
      if (callbackComplete) {
        callbackComplete(LoadResult.WRONG_HEADER_BITS_PER_PIXEL, null, 0, null);
      }
      return false;
    }
    // slice start
    bufOff += SIZE_SHORT;

    // grid spacing
    // const pixdim0 = this.readFloatFromBuffer(bufBytes, bufOff);
    //bufOff += SIZE_DWORD;
    if (DEBUG) console.log("buff offset: ", bufOff)
    const pixdim1 = this.readFloatFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_DWORD;
    const pixdim2 = this.readFloatFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_DWORD;
    const pixdim3 = this.readFloatFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_DWORD;
    const pixdim4 = this.readFloatFromBuffer(bufBytes, bufOff);
    bufOff += SIZE_DWORD;
    const pixdim5 = this.readFloatFromBuffer(bufBytes, bufOff);
    if (DEBUG) {
      console.log(`Nifti pixdim1: ${pixdim1}`);
      console.log(`Nifti pixdim2: ${pixdim2}`);
      console.log(`Nifti pixdim3: ${pixdim3}`);
      console.log(`Nifti pixdim4: ${pixdim4}`);
      console.log(`Nifti pixdim5: ${pixdim5}`);
    }

    //bufOffset is at 92 here, grapped voxOffset at the beginning
    bufOff += 31;

    const xyzt_units = bufBytes[bufOff];
    if (DEBUG) console.log("xyzt_units: ", xyzt_units);
    this.m_boxSize.x = this.m_xDim * pixdim1;
    this.m_boxSize.y = this.m_yDim * pixdim2;
    this.m_boxSize.z = this.m_zDim * pixdim3;
    const TOO_SMALL_SIZE = 1.0e-5;
    if (this.m_boxSize.x < TOO_SMALL_SIZE) {
      const SOME_MAGIC_BOX_DIM = 312.0;
      this.m_boxSize.x = SOME_MAGIC_BOX_DIM;
      this.m_boxSize.y = SOME_MAGIC_BOX_DIM;
      this.m_boxSize.z = SOME_MAGIC_BOX_DIM;
    }
    if (DEBUG) console.log(`Nifti physic volume size: ${this.m_boxSize.x} * ${this.m_boxSize.y} * ${this.m_boxSize.z}`);

    // 4 last bytes are magic
    bufOff = NIFTI_HEADER_SIZE - SIZE_DWORD;
    // 'n' == 110, '+' == 43, '1' == 49
    const MAG_0 = 110;
    const MAG_1 = 43;
    const MAG_2 = 49;
    const isCorrectMagic = (bufBytes[bufOff + 0] === MAG_0) &&
      // eslint-disable-next-line
      (bufBytes[bufOff + 1] === MAG_1) &&
      // eslint-disable-next-line
      (bufBytes[bufOff + 2] === MAG_2);
    if (!isCorrectMagic) {
      // eslint-disable-next-line
      console.error(`Nifti hdr bad magic: ${bufBytes[bufOff + 0]}, ${bufBytes[bufOff + 1]}, ${bufBytes[bufOff + 2]}`);
      if (callbackComplete) {
        callbackComplete(LoadResult.WRONG_HEADER_MAGIC, null, 0, null);
      }
      return false;
    }
    bufOff += SIZE_DWORD; // last magic bytes in header

    //note that below variable assumes that each phase contains the same amount of data
    const numVoxelsPerPhase = this.m_xDim * this.m_yDim * this.m_zDim;
    const numVoxels = numVoxelsPerPhase * this.phases;
    const dataOff = vox_offset; //when using nii, the first byte of data is stored at this offset
    const PHASE_OFFSET = (bufLen - dataOff) / this.phases;
    // get power of 2 for data size
    const scale = new Array(this.phases);
    const ACC_DEGREE = 9;


        let i, j;
    // scan min max in array
        const valMax = Array(this.phases).fill(0);
        j = 0;
        if ((dataType === NIFTI_DATA_TYPE_INT16) || (dataType === NIFTI_DATA_TYPE_UINT16)) {
            for (let p = 0; p < this.phases; p++) {
                j = p*PHASE_OFFSET;
                for (i = 0; i < numVoxels; i++) {
                    const val = this.readShortFromBuffer(bufBytes, dataOff + j);
                    // eslint-disable-next-line
                    j += 2;
                    if (val > valMax[p]) {
                    valMax[p] = val;
                    }
                    // progress update
                    if (callbackProgress && numVoxels % (j*4) === 0) {
                        const ratio = (j / numVoxels) / 4;
                        if (DEBUG) console.log("finding max progress, cap at 25%: ", ratio);
                        callbackProgress(ratio);
                    }
                } // for (i) al voxels
            }
        }
        if (dataType === NIFTI_DATA_TYPE_FLOAT32) {
            for (let p = 0; p < this.phases; p++) {
                j = p*PHASE_OFFSET;
                for (i = 0; i < numVoxels; i++) {
                    const fval = this.readFloatFromBuffer(bufBytes, dataOff + j);
                    const val = Math.floor(fval) + 1;
                    // eslint-disable-next-line
                    j += 4;
                    if (val > valMax[p]) {
                    valMax[p] = val;
                    }
                    // progress update
                    if (callbackProgress && numVoxels % (j*4) === 0) {
                        const ratio = (j / numVoxels) / 4;
                        if (DEBUG) console.log("finding max progress, cap at 25%: ", ratio);
                        callbackProgress(ratio);
                    }
                } // for (i) al voxels
            }
        }

        // console.log(`Nifti 16 data max val: ${valMax}`);

        // create histogram
        // const histArray = new Int32Array(valMax);
        let histArray = [];
        for (let p = 0; p < this.phases; p++) {
            histArray[p] = new Float32Array(valMax[p] + 1);
            for (i = 0; i < valMax[p] + 1; i++) {
              histArray[p][i] = 0.0;
            }
        }
        j = 0;
        if ((dataType === NIFTI_DATA_TYPE_INT16) || (dataType === NIFTI_DATA_TYPE_UINT16)) {
            for (let p = 0; p < this.phases; p++) {
                j = p*PHASE_OFFSET;
                for (i = 0; i < numVoxelsPerPhase; i++) {
                    const val = this.readShortFromBuffer(bufBytes, dataOff + j);
                    // eslint-disable-next-line
                    j += 2;
                    histArray[p][val] ++;
                }
            }
        } // if
        if (dataType === NIFTI_DATA_TYPE_FLOAT32) {
            for (let p = 0; p < this.phases; p++) {
                j = p*PHASE_OFFSET;
                for (i = 0; i < numVoxelsPerPhase; i++) {
                    const val = Math.floor(this.readFloatFromBuffer(bufBytes, dataOff + j));
                    // eslint-disable-next-line
                    j += 4;
                    histArray[p][val] ++;
                }
            }
        } // if
    const TOO_MIN_SCALE = 4;
    const histogram = new Array(this.phases);
    if ((dataType === NIFTI_DATA_TYPE_INT16) || (dataType === NIFTI_DATA_TYPE_UINT16)) {
        for (let p = 0; p < this.phases; p++) {
            histogram[p] = new UiHistogram();
            histogram[p].assignArray(valMax[p], histArray[p]);
            const HIST_SMOOTH_SIGMA = 0.8;
            const NORMALIZATION_APPLY = false;
            histogram[p].smoothHistogram(HIST_SMOOTH_SIGMA, NORMALIZATION_APPLY);

            const VAL_MIN = 4.0;
            // fix max index if maximum not found
            let indMax = histogram[p].getLastMaxIndex(VAL_MIN);
            if (indMax < 4) {
                indMax = valMax[p];
            }
            if (DEBUG) console.log(`LoaderNifti. get Last max peak: ${indMax} / ${histogram[p].m_numColors}`);
            scale[p] = (MAX_BYTE << ACC_DEGREE) / indMax;
            if (scale[p] <= TOO_MIN_SCALE) {
                console.error('Bad scaling: image will be 0');
                if (callbackComplete) {
                callbackComplete(LoadResult.ERROR_PROCESS_HISTOGRAM, null, 0, null);
                }
                return false;
            }
        }
    }

    // convert 16 bit data to 8 bit array
    const BITS_IN_BYTE = 8;
    const dataSize = numVoxels * (bitPix / BITS_IN_BYTE);
    let dataArray = [];
    for (let p = 0; p < this.phases; p++) {
      dataArray[p] = new Uint8Array(numVoxelsPerPhase);
    }

    i=0
    j=0;
    if ((dataType === NIFTI_DATA_TYPE_INT16) || (dataType === NIFTI_DATA_TYPE_UINT16)) {
        for (let p = 0; p < this.phases; p++) {
            j = p*PHASE_OFFSET;
            for (i = 0; i < numVoxels; i++) {
                let val = this.readShortFromBuffer(bufBytes, dataOff + j);
                // eslint-disable-next-line
                j += 2;
                // scale down to [0..255]
                val = (val * scale[p]) >> ACC_DEGREE;
                // check [0..255] range for some voxels out from histogram peak
                val = (val <= MAX_BYTE) ? val : MAX_BYTE;
                dataArray[p][i] = val;
                // progress update
                if (callbackProgress && numVoxels % (j*4) === 0) {
                    const ratio = 0.5 + (j / numVoxels) / 2;
                    if (DEBUG) console.log("filling data, 50%-100%: ", ratio);
                    callbackProgress(ratio);
                }
            } // for (i) all voxels
        }
    } // if 16 bit
    else if ((dataType === NIFTI_DATA_TYPE_INT8) || (dataType === NIFTI_DATA_TYPE_UINT8)) { // if 8 bit
        for (let p = 0; p < this.phases; p++) {
            j = p*PHASE_OFFSET;
            for (i = 0; i < numVoxelsPerPhase; i++, j++) {
                let val = bufBytes[dataOff + j];

                //support second file with spot data
                // let msk = mask ? mask[dataOff + j] : 0;

                //this is only module 4 data, so it will look like this [0, 1, 2, 3]
                // we map 3 to 255

                val *= 255/3 ; // 255 is divisible by 3? CRAZY.

                // if there is only one file, but it is 8 bit,
                // then this is a combined vasc/spot file, with vasc = 1 and spot > 1
                // if (val > 1) {
                //   msk = 1;
                //   val = 1;
                // }

                // js is beautiful and super readable so I wont even explain this next part
                //val = (msk && MAX_BYTE) || (val && Math.floor(MAX_BYTE*spotThreshold)); // ensures no part of the spot is cut out
                //val = val && ( msk ? MAX_BYTE : Math.floor(MAX_BYTE*0.99)); // functionally different but gives same result because the mask should be an exact overlay

                dataArray[p][i] = val;
                // progress update
                if (callbackProgress && numVoxels % (j*4) === 0) {
                    const ratio = 0.5 + (j / numVoxels) / 2;
                    if (DEBUG) console.log("filling data, 50%-100%: ", ratio);
                    callbackProgress(ratio);
                }
              } // for (i) all voxels
        } // end phase loop

    } else throw new Error("BAD input dataType")



   let xyDim = this.m_xDim * this.m_yDim;
   let x; let y;
   let z;
   const zOffMin = 0 * xyDim;
   const zOffMax = (this.m_zDim - 1) * xyDim;
   const xOffMin = 0;
   const xOffMax = this.m_xDim - 1;
   const yOffMin = 0;
   const yOffMax = (this.m_yDim - 1) * this.m_xDim;
   for (let p = 0; p < this.phases; p++) {
        // clear borders
        for (y = 0; y < this.m_yDim; y++) {
        const yOff = y * this.m_xDim;
        for (x = 0; x < this.m_xDim; x++) {
          let off;
          off = zOffMin + yOff + x;
          dataArray[p][off] = 0;
          off = zOffMax + yOff + x;
          dataArray[p][off] = 0;
        } // for (x)
      }   // for (y)

      for (z = 0; z < this.m_zDim; z++) {
        const zOff = z * xyDim;
        for (y = 0; y < this.m_yDim; y++) {
          let off;
          off = zOff + (y * this.m_xDim) + xOffMin;
          dataArray[p][off] = 0;
          off = zOff + (y * this.m_xDim) + xOffMax;
          dataArray[p][off] = 0;
        }
      }
      for (z = 0; z < this.m_zDim; z++) {
        const zOff = z * xyDim;
        for (x = 0; x < this.m_xDim; x++) {
          let off;
          off = zOff + yOffMin + x;
          dataArray[p][off] = 0;
          off = zOff + yOffMax + x;
          dataArray[p][off] = 0;
        }
      }
    }

    //since we are now passing array in, just get first index for now
    // save to result volumes
    for (let p = 0; p < this.phases; p++) {
      volDst[p].m_xDim = this.m_xDim;
      volDst[p].m_yDim = this.m_yDim;
      volDst[p].m_zDim = this.m_zDim;

      const ONE = 1;
      volDst[p].m_bytesPerVoxel = ONE;
      volDst[p].m_dataArray = dataArray[p];
      volDst[p].m_dataSize = numVoxelsPerPhase;
      volDst[p].m_boxSize = this.m_boxSize;
      volDst[p].m_phase = p;
    }

    if (DEBUG) console.log(`Nifti header read OK. Volume pixels = ${this.m_xDim} * ${this.m_yDim} * ${this.m_zDim} `);

    // Finally invoke user callback after file was read
    const KTX_GL_RED = 0x1903;
    const KTX_UNSIGNED_BYTE = 0x1401;
    const header = {
      m_pixelWidth: this.m_xDim,
      m_pixelHeight: this.m_yDim,
      m_pixelDepth: this.m_zDim,
      m_glType: KTX_UNSIGNED_BYTE,
      m_glTypeSize: 1,
      m_glFormat: KTX_GL_RED,
      m_glInternalFormat: KTX_GL_RED,
      m_glBaseInternalFormat: KTX_GL_RED,
    };
    if (callbackProgress) {
      callbackProgress(1.0);
    }
    if (callbackComplete) {
      callbackComplete(LoadResult.SUCCESS, header, dataSize, dataArray);
    } // if callbackComplete ready

    return true;
  } // end of readFromBuffer
  /**
  *
  * Read Nifti file from URL
  * @param {object} volDst volume to read
  * @param {string} strUrl from where
  * @param {Function} callbackProgress invoke during loading
  * @param {Function} callbackComplete invoke at the end with final success code
  */
  readFromUrl(volDst, strUrl, callbackProgress, callbackComplete) {
    if (DEBUG) console.log(`LoadedNifti.staring read ${strUrl} `);
    this.m_fileLoader = new FileLoader(strUrl);
    this.m_fileLoader.readFile((arrBuf) => {
      const okRead = this.readFromBuffer(volDst, arrBuf, callbackProgress, callbackComplete);
      return okRead;
    }, (errMsg) => {
      if (DEBUG) console.log(`LoadedNifti.Error read file: ${errMsg} `);
      callbackComplete(LoadResult.ERROR_CANT_OPEN_URL, null, 0, null);
    });
    return true;
  } // end of readFromUrl

} // end class LoaderNifti

export default LoaderNifti;