import React, { useContext, useEffect, useState } from 'react';
import { Dropdown as BsDropdown } from 'react-bootstrap';
import { StoreActionType } from '../../context/constants';
import { AppContext } from '../../context/context';

interface DropdownProps {
  label: string,
  defaultValue: number,
  options: string[],
  reset: boolean,
  module: string,
  name: string
}

export default function Dropdown({ label, reset, defaultValue, options, module, name }: DropdownProps) {
  const [state, dispatch] = useContext(AppContext);
  // Hecking typescript
  // @ts-ignore
  const optionModule = state.options[module as keyof Options]
  const value = optionModule[name]

  useEffect(() => {
    if (reset) {
      dispatchValue(options[defaultValue])
    }
  }, [reset])

  const dispatchValue = (value: string) => {
    dispatch({
      type: StoreActionType.SET_INPUT,
      payload: {
        module, name, value
      }
    })
  }

  const handleChange = (key: any, e: any) => {
    console.log(e)
    if (e?.target?.text) {
      dispatchValue(e.target.text);
    }
  }

  return (

    <div className="flex left column mr-2">
      {label}
      <BsDropdown id="dropdown-basic-button" onSelect={handleChange}>
        <BsDropdown.Toggle id={JSON.stringify(Math.random())}>{value}</BsDropdown.Toggle>
        <BsDropdown.Menu >
          {options.map((option, index) => (
            <BsDropdown.Item href={`#/action-${index}`}>{option}</BsDropdown.Item>)
          )}
        </BsDropdown.Menu>
      </BsDropdown>
    </div>
  )
}