import React, { useContext, useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
import DoctorView from './components/doctor/DoctorView';
import Home from './components/Home';
import NurseView from './components/nurse/NurseView';
import RadiologistView from './components/radiologist/RadiologistView';
import Navbar from './components/shared/Navbar';
import StoreActionType from './context/constants/ActionTypes';
import Notification from './components/shared/Notification';
import { AppContext } from './context/context';


function App() {
  const [state, dispatch] = useContext(AppContext)


  useEffect(() => {
    dispatch({ type: StoreActionType.SET_PATIENT_DATA, payload: [
      [
        "355056963916367154422305871670634494196",
        "2021-03-23T18:44:08-07:00",
        null,
        5,
        100,
        "0",
        false,
        "C:\\Users\\Keenan\\Git\\carl-server\\data\\Study2",
        null,
        null,
        null,
        null,
        null,
        "2020-01-01T09:00:00-08:00"
      ]
    ] });
    dispatch({ type: StoreActionType.UPDATE_BIOMARKER_DATA, payload: [
      [
        "355056963916367154422305871670634494196",
        "V0",
        5
      ]
    ] });
    dispatch({ type: StoreActionType.UPDATE_BIOMARKER_DATA, payload: [
      [
        "355056963916367154422305871670634494196",
        "K1",
        0.00148
      ]
    ] });
   }, [dispatch])


  return (
    <div className="App flex column">
      <Navbar />
      <Switch>
        <Route path="/frontend-demo/doctor">
          <DoctorView></DoctorView>
        </Route>
        <Route path="/frontend-demo/radiologist">
          <RadiologistView></RadiologistView>
        </Route>
        <Route path="/frontend-demo/nurse">
          <NurseView></NurseView>
        </Route>
        <Route exact path="/frontend-demo/">
          <Home />
        </Route>
      </Switch>
      <Notification notification={state.notification} />
    </div>
  );
}

export default App;
