
export const formatPatientData = (records: any) => {
  return records.map((record: any) => ({
    scanId: record[0],
    startTime: record[1] ? new Date(record[1]) : new Date(),
    endTime: record[2] ? new Date(record[2]) : new Date(),
    currentModule: record[3],
    progress: record[4],
    mlStatus: record[5],
    failed: record[6],
    initiator: records[7],
    outputLocation: record[8],
    phases: record[9],
    patientName: record[10],
    patientAge: record[11],
    patientSex: record[12],
    scanTime: record[13] ? new Date(record[13]) : new Date(),
  })
  )
}

export const formatBiomarkerData = (records: any) => {
  return records.map((record: any) => ({
    scanId: record[0],
    marker: record[1],
    value: record[2],
    postTime: record[3] ? new Date(record[3]) : new Date(),
  })
  )
}

export const formatDateString = (date: Date) => {
  const dateString = date.toLocaleDateString();
  const timeString = `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
  return `${dateString} ${timeString}`;
}