import axios from 'axios';

// eslint-disable-next-line
import file from '../assets/module4demoSubset.nii.gz';
export default class httpClient {

  async getFileName(scanId: string) {
    try {
      return await axios.get(file, {
        responseType: 'blob',
        params: {
          scan_id: scanId,
          tc: (new Date()).getTime()
        }
      })
    }
    catch (e) {
      return e
    }
  }

  async getTiming(scanId: string) {
    return JSON.parse(`[
      5e-05,
      2.1e-05,
      2e-05,
      3e-05,
      2e-05,
      2e-05,
      2e-05,
      3e-05,
      2e-05,
      2.1e-05,
      3e-05,
      2e-05,
      2e-05,
      6e-05,
      6.1e-05,
      5e-05,
      6e-05,
      6.1e-05
    ]`);
    /*
    axios.get('http://localhost:5000/file_retriever/timing', {
      responseType: 'json',
      params: {
        scan_id: scanId,
        tc: (new Date()).getTime()
      }
    })
    */
  }
}