import { IconProp } from "@fortawesome/fontawesome-svg-core";
import VolumeSet from "../components/visualization/engine/VolumeSet";

export interface State {
  showLoader: boolean,
  fileName: string,
  doctorView: DoctorView,
  nurseView: NurseView,
  visState: VisualizerState,
  socket: SocketIOClient.Socket,
  notification: Notification,
  options: Options
}

export interface Options {
  moduleOne: ModuleOneOptions,
  moduleTwo: ModuleTwoOptions,
  moduleThree: ModuleThreeOptions,
  moduleFour: ModuleFourOptions
}

export interface NurseView {
  patients: PatientData[],
  status: string,
  isLoaded: boolean,
  rateOfLeakage: number,
  volumeOfLeakage: number,
  timeOfStroke: number,
  title: string,
  date: Date
}

export interface DoctorView {
  patients: PatientData[],
  status: string,                  // 'bleeding' or 'bleedingnt'
  isLoaded: boolean,
  rateOfLeakage: number,           // k1
  volumeOfLeakage: number,         // v0 (only for storge purposes not to be displayed)
  timeOfStroke: number,              // v0/k1
  title: string,
  date: Date,
  loadedScan: string,
  phaseTimes: Array<number>,
  zoom: number
}

export interface PatientData {
  scanId: string,
  startTime: Date,
  endTime: Date,
  currentModule: number,
  progress: number,
  mlStatus: string,
  failed: boolean,
  initiator: number,
  outputLocation: string,
  phases: number,
  patientName: string,
  patientAge: string,
  patientSex: string,
  scanTime: Date
}

export interface VisualizerState {
  spotColor: [number, number, number], // [r, g, b]
  volumeOpacity: number,
  showVenousStructure: boolean,
  volumeSet: VolumeSet,
  volumeIndex: number,
  texture3d: any,
  modeView: number,
  loadingBarRatio: number,
  mode2d: number,
  slider2d: number,
  slider3d_r: number,
  slider3d_g: number,
  slider3d_b: number,
  mode3d: number,
  mode3droi: number,
  sliderOpacity: number,
  sliderIsosurface: number,
  sliderBrightness: number,
  sliderCut: number,
  sliderQuality: number,
  sliderErRadius: number,
  sliderErDepth: number,
  volumeRenderer: any,
  indexTools2d: number,
  render2dZoom: number,
  render2dxPos: number,
  render2dyPos: number,
  graphics2d: any,
  uiApp: any,
  dicomInfo: any,
  isTool3D: boolean,
  sliderContrast3D: any,
  arrErrors: [],
  dicomSeries: [],
  loaderDicom: any,
}

export interface Notification {
  body: string,
  header: string,
  isVisible: boolean,
  icon: IconProp | null,
  status: NotificationStatus | null,
  spin: boolean,
  timer: number | null
}

export enum NotificationStatus {
  SUCCESS = 'success',
  FAILURE = 'failure'
}

export interface ModuleOneOptions {
  processTimingData: boolean,
  build4d: boolean,
  build3d: boolean,
  compute4dSummary: boolean,
  saveAvailableData: boolean,
  reloadTimingIndices: boolean,
  stopIfOptionsChange: boolean,
  autoSelectLargest: boolean,
  autoSelectFirstLargest: boolean,
  autoSelectLastLargest: boolean,
  setTimeSeparationMin: string,
  setTimeSeparationMax: string,
  imageSetIndex: string
}

export interface ModuleTwoOptions {
  dataOutput: string
  modality: string,
  vendor: string,
  scanner: string,
  process: string,
  region: string,
  study: string,
  restartEnabled: boolean,
  minimum3d: string,
  includeMinThreshold: boolean,
  includeMaxThreshold: boolean,
  useMorphologicalOperations: boolean,
  strfiDisk: string,
  strfi3d: string,
  imageRegion: string,
  minimumConnected: string,
  coarseRadius: string,
  strfiRadius: string,
  imageIndexRange: string
}

export interface ModuleFourOptions {
  enableRestart: boolean,
  vascularMaskDirectory: string,
  outputDataDirectory: string,
}

export interface ModuleThreeOptions {
  minimumNumber: string
  numberOfBins: string,
  distributionTail: string,
  feature: string,
  medianMin: string,
  medianMax: string,
  peakRangeMin: string,
  peakRangeMax: string,
  probabilityMin: string,
  probabilityMax: string,
  nonNegativeValues: boolean,
  nonZeroValues: boolean
}