import React, { useContext } from 'react';
import StoreActionType from '../../context/constants/ActionTypes';
import { AppContext } from '../../context/context';
import { PatientData } from '../../context/types';
import { runFileWorker } from '../../utils/fileWorkerUtil';
import { formatDateString } from '../../utils/format';
import LoadingCircle from './LoadingCircle';
import './Shared.css';

interface PatienListProps {
  isNurse: boolean;
}

export default function PatientList({ isNurse }: PatienListProps) {
  const [state, dispatch] = useContext(AppContext)
  const patients = isNurse ? state.nurseView.patients : state.doctorView.patients

  const getModuleName = (module: number) => {
    switch (module) {
      case 0:
        return "Initializing"
      case 1:
        return "Preprocessing"
      case 2:
        return "Extract Brain Region"
      case 3:
        return "Vascular Segmentation"
      case 4:
        return "Dynamic Spot Selection"
      case 5:
        return "Completed"
    }
  }

  const runFileWorkerClick = async (scanId: string) => {
    if (state.doctorView.loadedScan !== scanId)
      return await runFileWorker(dispatch, scanId);
    else
      return dispatch({ type: StoreActionType.SET_IS_LOADED, isLoaded: true });
  }

  return (
    <div className="file-list fill">
      <div className="col-12 px-0 py-4 flex center top">
        <div className="col-8 p-0 flex center top ">
          <h3 className="header">Patient Files</h3>
          {!patients.length &&
            <div className="col-12 flex space-around">
              <div className="col-12" style={{'color': 'grey'}}>
                No {isNurse ? 'in-progress' : 'completed'} scans.
              </div>
            </div>
          }
          {!!patients.length && patients.map((patient: PatientData) => (
            <div key={patient.scanId}
              className="col-12 flex space-between file-item"
              onClick={async () => await runFileWorkerClick(patient.scanId) }>
              <div className="col-5 flex left" >
                {patient.patientName || patient.scanId}
              </div>
              <div className="col-3 flex left">
                {formatDateString(patient.startTime)}
              </div>
              <div className="col-4 p-0 flex right">
                <div className="pr-1">
                  ({getModuleName(patient.currentModule)})
                </div>
                {patient.progress}%
                <LoadingCircle radius={10} stroke={2} progress={patient.progress} />
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}