import React from 'react';
import { Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

export default function Home() {
  return (<div className="col-12 p-0 flex fill center">
    <h2 className="header">I am a</h2>
    <div className="col-12 p-0 flex center">
      <Button className="m-2 button-main button-large"> <Link to="/frontend-demo/doctor">Doctor</Link></Button>
      <Button className="m-2 button-main button-large"> <Link to="/frontend-demo/nurse">Nurse</Link> </Button>
      <Button className="m-2 button-main button-large"> <Link to="/frontend-demo/radiologist">Radiologist</Link> </Button>
    </div>

  </div>)
}